$font-family: Inter, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", "Noto Sans",
"Liberation Sans", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol",
"Noto Color Emoji";

$bg-dark-5: rgb(17, 24, 39);
$bg-dark-3: rgb(35, 45, 59);
$background-primary: radial-gradient(ellipse at top, $bg-dark-3, $bg-dark-5);

$text-color-3: rgb(156, 163, 175);
$text-color-1: rgb(255, 255, 255);
$text-hover-color-1: darken($text-color-1, 15%);
$text-active-color-1: darken($text-color-1, 30%);
$text-disabled-color-1: darken($text-color-1, 40%);

$btn-bg-1: rgba(64, 74, 207, 0.8);
$btn-bg-2: rgba(75, 50, 216, 0.8);
$btn-hover-bg-1: darken($btn-bg-1, 15%);
$btn-active-bg-1: darken($btn-bg-1, 30%);
$btn-disabled-bg-1: darken($btn-bg-1, 40%);
$btn-background-primary: linear-gradient(135deg, $btn-bg-1, $btn-bg-2);

$border-color: rgba(229, 231, 235, 0.164);
$card-border-color-1: $border-color;
$card-bg-1: $bg-dark-3;
$max-width-rem-values: 50, 55, 60, 65, 70;

